export default {
	data() {
		return {
			inputValue: ''
		}
	},
	computed: {
		strLeft () {
			return 500 - this.inputValue.length
		}
	},
	methods: {
		handleSubmit (e) {
			this.$emit('reviewSubmit', this.inputValue)
			this.inputValue = ''
			console.log('sub')
		},
		handleBlur () {
			this.$emit('reviewBlur', this.inputValue)
			this.inputValue = ''
		}
	}
